.rock-slider.rock-slider-swipe-content {
  width: 100%;
  height: auto;
  background: transparent;
  display: block;
}

.rock-slider.rock-slider-swipe-content .rock-inner .rock-slide {
  -ms-filter: "progid:DXImageTransform@mixin Microsoft@mixin Alpha(Opacity=${iefactor})";
  filter: "alpha(opacity=(${iefactor}))";
  -moz-opacity: 1;
  -khtml-opacity: 1;
  opacity: 1;
  display: block;
  position: relative;
}

.rock-slider.rock-slider-swipe-content .rock-inner .rock-slide.active {
  z-index: 2;
}

.rock-slider.rock-slider-swipe-content .rock-inner .rock-swipe-wrapper {
  display: block;
  position: relative;
  float: left;
  overflow: hidden;
}

.rock-slider.rock-slider-swipe-content .rock-swipe-highlight {
  display: block;
  position: absolute;
  top: 0;
  width: 1px;
  height: 100%;
  margin: 0;
  padding: 0;
  z-index: 4;
}

.rock-slider.rock-slider-swipe-content .rock-swipe-highlight.animating {
  transition: all 0.7s ease;
}

.rock-slider.rock-slider-swipe-content .rock-swipe-highlight.left {
  left: -1px;
}

.rock-slider.rock-slider-swipe-content .rock-swipe-highlight.right {
  right: -1px;
}
